.changeHeight {
  height: 100%;
}
.failureDistribution {
  display: inline-block;
  width: 48%;
}
.title {
  text-align: center;
  font-size: 30px;
}
.failure-title {
  text-align: center;
}
.totalfailure-title {
  text-align: center;
  font-size: 20px;
}
.single-title {
  border-top: 1px solid #ccc;
  text-align: center;
  font-size: 20px;
}
.stop-title {
  border-top: 1px solid #ccc;
  text-align: center;
  font-size: 18px;
}
.stop-list > li {
  width: 30%;
  color: #aaa;
  list-style: square;
  font-weight: bold;
  font-size: 18px;
}

