body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.changeHeight {
  height: 100%;
}
.failureDistribution {
  display: inline-block;
  width: 48%;
}
.title {
  text-align: center;
  font-size: 30px;
}
.failure-title {
  text-align: center;
}
.totalfailure-title {
  text-align: center;
  font-size: 20px;
}
.single-title {
  border-top: 1px solid #ccc;
  text-align: center;
  font-size: 20px;
}
.stop-title {
  border-top: 1px solid #ccc;
  text-align: center;
  font-size: 18px;
}
.stop-list > li {
  width: 30%;
  color: #aaa;
  list-style: square;
  font-weight: bold;
  font-size: 18px;
}


.App {
  width: 100%;
  height: 100%;
}

